<template>
  <div class="question">
    <el-table :data="questionData">
      <el-table-column label="id" prop="id"></el-table-column>
      <el-table-column label="" prop="type">
        <template slot-scope="scope">
          <span>{{$store.state.types[scope.row.type]}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <div class="hidden" v-html="scope.row.question_name"></div>
        </template>
      </el-table-column>
      <el-table-column label="" prop="subject_name"></el-table-column>
      <el-table-column label="A" prop="a"></el-table-column>
      <el-table-column label="B" prop="b"></el-table-column>
      <el-table-column label="C" prop="c"></el-table-column>
      <el-table-column label="D" prop="d"></el-table-column>
      <el-table-column label="" prop="answer"></el-table-column>
      <el-table-column label="" prop="nickname"></el-table-column>
      <el-table-column label="" prop="create_time"></el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="scope">
          <el-button size="mini" type="warning" @click="$router.push(`/houtai/editQuestion/${scope.row.id}`)"></el-button>
          <el-button size="mini" type="danger" @click="delQuestion(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button type="primary" size="small" style="margin-top:10px" @click="$router.push('/houtai/addQuestion')"></el-button>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        questionData: null
      }
    },
    methods:{
      getQuestion(){
        this.axios.get(`${this.baseURL}/question/get`).then(res=>this.questionData=res.data.rows)
      },
      delQuestion(id){
        this.axios.post(`${this.baseURL}/question/delete`,{id}).then(res=>{
          if(res.data.success){
            this.$message(res.data.message)
            this.getQuestion()
          }
        })
      }
    },
    created(){
      this.getQuestion()
    }
  }
</script>

<style scoped lang="stylus">
.hidden
  height 80px
  line-height 20px
  overflow hidden
  text-overflow: ellipsis;
  display: -webkit-box; /*  */      
  -webkit-line-clamp: 4; /*  */      
  -webkit-box-orient: vertical; /*  */
  >>> p 
    margin 0
</style>